@value tablet-viewport,  mobile-viewport from "../../styles/breakpoints.css";

.field {
  width: 100%;
  height: 45px;
  padding: 10px 10px 10px 31px;

  font-size: 16px;
  line-height: 20px;
  color: var(--base-color);

  background-color: var(--contrast-color);
  border: none;
  border-radius: 6px;

  box-shadow: 0px 0px 6px 1px  var(--contrast-color);
  box-sizing: border-box  !important;

  transition: box-shadow var(--transition-duration) linear;
}

.field:hover,
.field:focus {
  box-shadow: 0px 0px 3px #F9F9FC;
  outline: none;
}

.field:placeholder {
  font-size: 16px;
  line-height: 20px;
  color: rgba(var(--base-color), 0.9);
}

@media tablet-viewport {
  .field{
    font-size: 14px;
    line-height: 16px;
  }

  .field:placeholder {
    font-size: 14px;
    line-height: 16px;
  }
}
